<template>
  <div id="loading">
    <img src="./loading.gif">
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'loading'
  }
</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  #loading {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1000;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    img {
      width: 64px;
      height: 64px;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
    }
  }
</style>


