  #error {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    box-sizing: border-box;
    width: 300px;
    padding: 0 10px;
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    font-weight: bolder;
    text-align: center;
    background-color: rgba(0, 0, 0, .5);
    color: #0f0;
  }
</style>


