import axios from 'axios'

export function getHistoryList (city = 'all', time = '') {
  let url = `http://www.cq.cn/x/air/tianqi4/index.php/play/getHistoryWea/search/${city}/time/${time}`
  return axios.get(url)
}

export function getSmogHistoryList (city = 'all', time = '') {
  let url = `http://www.cq.cn/x/air/tianqi4/index.php/play/getHistoryAqi/search/${city}/time/${time}`
  console.log(url)
  return axios.get(url)
}



